@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans&family=Roboto&display=swap);
* {
  margin: 0;
}
body {
  margin: 0;
  font-family: 'Merriweather Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.navbar {
  background-color: white;
  border-bottom: 1px solid;
  margin-bottom: 0;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.navbar img {
  height: 120px;
}

.main {
  background-color: #e5ecf1;
}

::selection {
  background: #d1e4f1;
}

button {
  background-color: #15b2f7;
}

.factors__dropdown {
  display: flex;
  align-items: center;
}

.factors__dropdown h2 {
  font-size: 18px;
  margin-right: 10px;
  cursor: pointer;
  margin-bottom: 0;
}

.factors__dropdown > .MuiSvgIcon-root {
  font-size: 20px;
  margin-top: 20px;
  margin-right: 15px;
}

.dropdown__padding {
  margin-left: 5%;
  margin-right: 5%;
}

/*contributing factors*/
.contributing__factor {
  margin-left: 10%;
  margin-right: 10%;
}

/*add factor*/
.factors__addFactor {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 0px;
}

.factors__addFactor h4 {
  margin: 0;
  padding: 0;
  font-size: 15px;
}

.factors__addFactor p {
  font-size: 12px;
  color: red;
  border-radius: 2px;
  margin: 0;
  margin-left: 80px;
  display: none;
}

.factors__addFactor div div {
  display: flex;
  align-items: center;
}

.factors__addFactor div div button {
  background-color: #15b2f7;
  border: none;
  display: flex;
  align-items: center;
  height: 40px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.factors__addFactor div div button:hover {
  background-color: #00a3e9;
}

.factors__addFactor .MuiSvgIcon-root {
  font-size: 20px;
}

.factors__addFactor div div input {
  width: 300px;
  height: 40px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.whys {
  background-color: #e5ecf1;
  margin: 0px;
  display: flex;
  flex-direction: column;
}

.whys > h3 {
  margin-left: 3%;
}

::selection {
  background: #d1e4f1;
}

.whys__addWhys {
  margin-left: 3%;
  margin-right: 3%;
}

.whys__evaluations {
  margin-left: 3%;
  margin-right: 3%;
}

#linethrough_text {
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #000;
  line-height: 0.1em;
  margin: 40px 0 0px;
}

#linethrough_text span {
  background: #e5ecf1;
  padding: 0 10px;
}

